<template>
 <v-menu 
   
    v-model="menu"
    :close-on-content-click="false"
    :nudge-right="40"
    transition="scale-transition"
    offset-y
    min-width="290px"
  >
    <template v-slot:activator="{ on }">
      <v-text-field
        v-bind="$attrs"
        readonly
        v-on="on"
        :value="date"
      ></v-text-field>
    </template>
    <v-date-picker v-model="date" @input="menu = false"  ></v-date-picker>
  </v-menu>
</template>
<script>
 

export default {
  name: 'date-picker',
  props: {
    value: String,
    collection: {
      type: String
    }
  },
  data() {
    
    return {
      menu: false,
      date: new Date().toISOString().substr(0, 10),
      loading: false
    }
  },
  watch: {
    date (date) {
      this.$emit('input', date)
    }
  }, 
 
}
</script>