export default  {
        title: 'Users',
        icon: 'mdi-account-multiple',
        table: [
            {
              text: 'Display Name',
              align: 'start',
              sortable: false,
              value: 'displayName',
            },
            { text: 'Username', value: 'username' },
            { text: 'Email', value: 'email' },
            { text: 'Phone', value: 'phone' },
            { text: 'Role', value: 'role' },
            { text: 'Active', value: 'active' },
            { text: 'Actions', value: 'actions', width: '100' } 
        ],
        form: [
            {
                label: 'General',
                fields: [
                    {
                        input: 'text',
                        name: 'displayName',
                        bind: {
                            label: "Display Name",
                            prependIcon: 'mdi-account',
                            required: true,
                            rules: [
                                v => !!v || 'Display Name is required',
                            ],
                        } 
                    },
                    {
                        input: 'text',
                        name: 'username',
                        bind: {
                            label: "Username",
                            autocomplete: 'username',
                            prependIcon: 'mdi-account-box',
                            required: true,
                            rules: [
                                v => !!v || 'Username is required',
                            ],
                        } 
                    },
                    {
                        input: 'password',
                        name: 'password',
                        bind: {
                            label: "Password",
                            prependIcon: 'mdi-lock',
                             
                            
                        } 
                    },
                    {
                        input: 'autocomplete',
                        name: 'role',
                        bind: {
                            label: "Role", 
                            collection:'role',
                            itemText: "name",
                            itemValue: "role",
                            prependIcon: "mdi-key",
                            required: true,
                            rules: [
                                v => !!v || 'Role is required',
                            ],
                        } 
                    },
    
                    {
                        input: 'text',
                        name: 'email',
                        bind: {
                            label: "Email",
                            prependIcon: 'mdi-email',
                             
                        } 
                    },
                    {
                        input: 'text',
                        name: 'phone',
                        bind: {
                            label: "Phone ",
                            prependIcon: 'mdi-phone',
                          
                        } 
                    },
                    {
                        input: 'text',
                        name: 'address',
                        bind: {
                            label: "Address", 
                           
                            prependIcon: "mdi-map-marker",
                             
                        } 
                    },
                    {
                        input: 'switch',
                        name: 'active',
                        default: true,
                        bind: {
                            label: "Active" 
                        } 
                    }
                ]
            }
        ]
    }